.i {
    display: flex;
    height: 100vh;
}

.i-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right {
    flex:1;
    position: relative;
}

.i-left-wrapper {
    padding: 50px;
    height: 50%;
}

.i-intro{
    font-size: 30px;
    font-weight: 300;
}

.i-name{
    font-size: 60px;
}

.i-title{
    height: 50px;
    overflow:hidden;
    margin: 15px 0px;
}

.i-title-wrapper{
    
    height: 100%;
    animation: move 5s ease-in-out infinite alternate;
}

@keyframes move {
    0%{
        transform: translateY(0px);
    }
    50%{
        transform: translateY(-50px);
    }
    100%{
        transform: translateY(-100px);
    }
}

.i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: rgb(33, 176, 33);
    display: flex;
    align-items: center;
}

.i-img {
    width: 50%;
    height: 50%;
    position: absolute;
    top: 25%;
    left: 35%;
}

.i-bg {
    clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    width: 100%;
    height: 100%;
    background-color: #59b256;
    position: absolute;
    top: 0;
    right: 0;
}